import React, { Component } from 'react';
import { Button } from 'antd';
import { withRouter, RouteComponentProps } from 'react-router';

class Index extends Component<RouteComponentProps> {
  /**
   * handleBackHome
   */
  public handleBackHome = () => {
    const { history } = this.props;
    history.push('/');
  };
  public render() {
    return (
      <div
        style={{
          height: '60vh',
          padding: 80,
          textAlign: 'center'
        }}
      >
        <img src={require('@/assets/noFound.svg')} alt="404" />
        <br />
        <br />
        <h1>404</h1>
        <p>对不起, 您访问的页面不存在.</p>
        <Button type="primary" onClick={this.handleBackHome}>
          首页
        </Button>
      </div>
    );
  }
}
export default withRouter(Index);
